<template>
  <div class="ticket-preview column is-clearfix">
    <h3 class="title is-4">Share Ticket To</h3>
    <form @submit.prevent="sendInvite">
      <b-field
        label="First Name"
        horizontal
        :type="{ 'is-danger': $v.invite.firstName.$error }"
        :message="
          $v.invite.firstName.$error ? 'Please enter the first name of the person you\'re inviting' : ''
        "
      >
        <b-input
          v-model="invite.firstName"
          placeholder="Leslie"
          :disabled="allocationReached"
          @blur="$v.invite.firstName.$touch()"
          type="text"
          icon="account"
        >
        </b-input>
      </b-field>

      <b-field
        label="Last Name"
        horizontal
        :type="{ 'is-danger': $v.invite.lastName.$error }"
        :message="$v.invite.lastName.$error ? 'Please enter the last name of the person you\'re inviting' : ''"
      >
        <b-input
          v-model="invite.lastName"
          placeholder="Miles"
          :disabled="allocationReached"
          @blur="$v.invite.lastName.$touch()"
          type="text"
          icon="account"
        >
        </b-input>
      </b-field>

      <b-field
        label="Email"
        horizontal
        :type="{ 'is-danger': $v.invite.email.$error }"
        :message="$v.invite.email.$error ? 'Please enter the email of the person you\'re inviting' : ''"
      >
        <b-input
          v-model="invite.email"
          placeholder="francis@example.com"
          :disabled="allocationReached"
          @blur="$v.invite.email.$touch()"
          type="email"
          icon="email"
        >
        </b-input>
      </b-field>

      <button
        class="add-button button is-primary is-medium is-pulled-right"
        type="submit"
        :disabled="allocationReached"
      >
        Send Ticket
      </button>
    </form>
  </div>
</template>

<script>
import { required, email } from "vuelidate/lib/validators";
import { mapState } from "vuex";

function notOwnerEmail(model) {
  return model !== this.$parent.person.email
}

export default {
  name: "RecipientDetailsForm",
  props: {
    allocationReached: {
      type: Boolean,
      required: true
    }
  },
  data() {
    return {
      invite: { ...this.newInvite, paidFor: true }
    };
  },
  validations: {
    invite: {
      firstName: { required },
      lastName: { required },
      email: {
        required,
        email,
        notOwnerEmail
      }
    }
  },
  computed: {
    ...mapState({
      newInvite: "invites/newInvite"
    })
  },
  methods: {
    sendInvite() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        this.$emit("add-invite", this.invite);
        this.invite = { ...this.newInvite, paidFor: true };
        this.$v.$reset();
      }
    }
  }
}

</script>

<style lang="scss">
@import "@/assets/scss/buefy-init.scss";

.ticket-preview {
  position: relative;
  border: 3px dashed $primary;
  padding: 2rem;
  margin: 1rem;
}
</style>
